import { Component } from '@angular/core';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent {

  title = 'Simple form :';
  formTitle = 'Inscription'

  goSignin = () => {
    this.formTitle = "Inscription"
  };
  goLogin = () => {
    this.formTitle = "Connexion"
  };
}



