import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { CustomValidators } from '../custom-validators'

@Component({
  selector: 'app-form1',
  templateUrl: './form1.component.html',
  styleUrls: ['./form1.component.css']
})
export class Form1Component implements OnInit {
  @Input()
  formTitle: String;

  constructor(public fb: FormBuilder) {}
  public signinForm = this.fb.group({
    lstName: ['', [Validators.required, CustomValidators.noWhiteSpaces, Validators.pattern(CustomValidators.onlyLettersPattern)]],
    fstName: ['', Validators.pattern(CustomValidators.onlyLettersPattern)],
    gender: [null, Validators.required],
    power: ['', Validators.required],
    comments: ['', [Validators.required, CustomValidators.noWhiteSpaces]],
    newsletter: [false]
  });
  title = 'Simple form :';
  formData = {newsletter: false, power: ''};
  powers = [{label: 'Emploi 1', value: '1'}, {label: 'Emploi 2', value: '2'},
            {label: 'Emploi 3', value: '3'}, {label: 'Emploi 4', value: '4'}];
  testFct = function(){
    //console.log(this.mockForm);
    
    console.log('BLLLLAAH', this.signinForm.get('lstName'))
    console.log('=> Signin form value', this.signinForm.value)
  }

  ngOnInit(){

  }

}



